import React from 'react'
import { Link } from 'react-router-dom'

export const CourseInprogress = (props) => {
    const {course} = props
    return (
        <div className="InProgressCourses">
                <div className="center">
                    <div className="Inprogress">
                        <section>
                            <header>
                                <div className="CourseHeader">
                                    <div className="header-content">{course.subject}</div>
                                </div>
                            </header>
                            <div className="course-main-content">
                                <div className="course-main-content-center row">
                                    <div className="course-main-content-primary col-md-6 col-12">
                                        <Link to={`detail/${course._id}`}>
                                            <h1 className="course-main-content-title">
                                                {course.title}
                                            </h1>
                                        </Link>
                                        <span className="course-pill">Chưa hoàn thành</span>
                                    </div>
                                    <div className="course-main-content-secondary col-md-6 col-12">
                                        <div className="course-main-content-button">
                                            <div className="Course-link-button">
                                                <Link to={`detail/${course._id}`} >
                                                    <span>Đến khóa học</span>
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <footer className="Course-footer">
                                <div className="partner-name">
                                    <div>{course.university}</div>
                                </div>
                            </footer>
                        </section>
                    </div>
                </div>
            </div>
    )
}

