import React, { useState, useEffect } from "react";
import Axios from "axios";
import { nodeApiLink } from "../config/NodeServerLink";
import { CourseInprogress } from "../Component/CourseInprogress";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";

export const Inprogress = () => {
    const userOwnCourses =
        useSelector((state) => state.userStatus.userInfo && state.userStatus.userInfo.ownCourses) || [];
    const auth = useSelector((state) => state.userStatus.auth);
    const history = useHistory();
    const [ownCourses, setOwnCourses] = useState([]);
    const getOwnCourses = async () => {
        const { data } = await Axios.get(`${nodeApiLink}/api/user/owncourse`, { withCredentials: true });
        setOwnCourses(data);
    };
    useEffect(() => {
        getOwnCourses();
        return () => {
            //
        };
    }, [userOwnCourses.length]);
    useEffect(() => {
        if (!auth) history.push("/");
        return () => {
            //
        };
    }, [auth, history]);

    return (
        <div className="container-fluid" style={{minHeight:700,backgroundColor: '#f3f3f3'}} >
            {ownCourses &&
                ownCourses.map((course) => {
                    return <CourseInprogress key={course._id} course={course} />;
                })}
        </div>
    );
};
export default Inprogress;
